"use client";
import React, { useEffect, useState } from 'react'
import { useParams } from 'next/navigation';
import Image from 'next/image';

const page = () => {
  const { id } = useParams();
  const [artist, setArtist] = useState(null);
  const fetchArtist = async () => {
    const res = await fetch(`http://localhost:8000/find-artist/${id}`, {
      method: 'GET'
    });
    if (res.status !== 200) {
      throw Error('Something went wrong');
    }
    const data = await res.json();
    setArtist(data);
  }
  useEffect(() => {
    fetchArtist();
  })
  return (
    <div className="bg-black min-h-screen text-white flex">
      <aside className="w-64 bg-black p-6 hidden md:block flex-shrink-0">
        <div className="font-bold text-2xl mb-10">Spotify</div>
        <nav>
          <ul>
            <li className="mb-4">
              <a href="#" className="flex items-center text-zinc-400 hover:text-white transition-colors duration-300">
                <svg className="w-6 h-6 mr-3" fill="currentColor" viewBox="0 0 20 20"><path d="M10.707 2.293a1 1 0 00-1.414 0l-7 7a1 1 0 001.414 1.414L4 10.414V17a1 1 0 001 1h2a1 1 0 001-1v-2a1 1 0 011-1h2a1 1 0 011 1v2a1 1 0 001 1h2a1 1 0 001-1v-6.586l.293.293a1 1 0 001.414-1.414l-7-7z"></path></svg>
                Home
              </a>
            </li>
            <li className="mb-4">
              <a href="#" className="flex items-center text-zinc-400 hover:text-white transition-colors duration-300">
                <svg className="w-6 h-6 mr-3" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"></path></svg>
                Search
              </a>
            </li>
            <li>
              <a href="#" className="flex items-center text-zinc-400 hover:text-white transition-colors duration-300">
                <svg className="w-6 h-6 mr-3" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 19V6l12-3v13M9 19c0 1.105-1.343 2-3 2s-3-.895-3-2 1.343-2 3-2 3 .895 3 2zm12-13c0 1.105-1.343 2-3 2s-3-.895-3-2 1.343-2 3-2 3 .895 3 2zM9 6l12 3"></path></svg>
                Your Library
              </a>
            </li>
          </ul>
        </nav>
      </aside>
      <div className='flex-1 h-[calc(100vh-80px)'>
        {
          artist && <>
            <div className='bg-gradient-to-b from-[#EDEDED] to-[#989898] pt-4 rounded-xl'>
            <div className='h-[40vh] flex gap-12 items-end p-4'>
              <div>
                <img src={artist?.photoUrl} alt={artist?.name || "Artist"} className='rounded-full object-cover h-[250px] w-[250px] shadow-2xl shadow-[#]' />
              </div>
              <div className='gap-4 flex flex-col'>
                <h1 className='text-8xl text-black font-extrabold'>{artist?.name}</h1>
                <h3 className='text-black'>33,412,955 monthly listeners</h3>
              </div>
            </div>
            <div className='px-4 mt-4 rounded-b-xl'>
            </div>
          </div>
          <div className='px-4 py-13 flex flex-col gap-5 hover:bg-[#1F1F1F]'>
            {
              artist?.songId?.map((song) => {
                return <div
                
                key={song?._id}>
                  <p className='text-white'>{song?.title}</p>
                </div>
              })
            }

            </div>
          </>
        }
      </div>
    </div>
  )
}

export default page